import dayjs from 'dayjs'
import { useAppSelector } from '../../../hooks/useAppSelector'
import { getTrendads } from '../../../redux/slice'
import { dataIntegrated, ItrendData } from '../../../types/trendData.d'

const GetData = () => {
  const DATA = useAppSelector(getTrendads)
  // ROAS, , , ,  , 
  const ROAS: dataIntegrated[] = [] // roas
  const cost: dataIntegrated[] = [] // 
  const imp: dataIntegrated[] = [] // 
  const click: dataIntegrated[] = [] // 
  const conversion: dataIntegrated[] = [] // cvr*click->
  const sales: dataIntegrated[] = [] // cost*roas/100->
  // ROAS = (   /   ) x 100
  // https://www.i-boss.co.kr/ab-6141-52669
  DATA.forEach((d: ItrendData) => {
    const dateForm = dayjs(d.date).format('M D')
    ROAS.push({ x: dateForm, y: d.roas, label: `${dateForm}, ROAS: ${d.roas}` })
    cost.push({ x: dateForm, y: d.cost, label: `${dateForm}, : ${d.cost}` })
    imp.push({ x: dateForm, y: d.imp, label: `${dateForm}, : ${d.imp}` })
    click.push({ x: dateForm, y: d.click, label: `${dateForm}, : ${d.click}` })
    conversion.push({
      x: dateForm,
      y: Math.floor((d.cvr * d.click) / 100),
      label: `${d.date}, : ${Math.floor(d.cvr * d.click)}`,
    })
    sales.push({
      x: dateForm,
      y: Math.floor((d.cost * d.roas) / 100),
      label: `${dateForm}, : ${Math.floor((d.cost * d.roas) / 100)}`,
    })
  })
  const all = [ROAS, cost, imp, click, conversion, sales]
  return { all }
}
export default GetData
