import { NavLink } from 'react-router-dom'
import { Board, Chart, Guide, LogoMad } from '../../assets/svgs'
import { cx } from '../../styles'
import DropDown from '../DropdownCompo/Dropdown'

import styles from './navi.module.scss'

const Navi = () => {
  return (
    <div className={styles.nav}>
      <header className={styles.logo}>
        <LogoMad />
      </header>
      <hr />
      <div className={styles.dropDown}>
        <p className={styles.text}></p>
        <DropDown orders={3} />
      </div>

      <div className={styles.navBtns}>
        <p className={styles.text}> </p>
        <NavLink className={({ isActive }) => cx(styles.navBtn, { [styles.isActive]: isActive })} to='/'>
          <Board />
          
        </NavLink>
        <NavLink className={({ isActive }) => cx(styles.navBtn, { [styles.isActive]: isActive })} to='/dashboard'>
          <Chart />
          
        </NavLink>
      </div>
      <aside>
        <button type='button' className={styles.infoGuide}>
          <Guide />
          <div className={styles.infoGuideText}>
            <h1 className={styles.infoGuideTitle}>  </h1>
            <p className={styles.text}>  </p>
          </div>
        </button>
        <p className={styles.text}>  .</p>
        <a href='www.google.com' className={styles.link}>
          
        </a>
      </aside>
    </div>
  )
}
export default Navi
