import styles from './adData.module.scss'
import setData from './setData'
import setIconColor from './setIconColor'
import setPositiveInteger from './setPositiveInteger'
import setSlice from './setSlice'
import setString from './setString'

const AdData = () => {
  const {
    ROAS,
    differROAS,
    cost,
    differcost,
    imp,
    differimp,
    click,
    differclick,
    conversion,
    differconversion,
    sales,
    differsales,
  } = setData()

  const adList = [
    {
      title: 'ROAS',
      value: `${setPositiveInteger(ROAS[0])}%`,
      upDownIcon: setIconColor(differROAS),
      upDownValue: `${setPositiveInteger(differROAS)}%`,
    },
    {
      title: '',
      value: `${setSlice(setString(cost[0]))} `,
      upDownIcon: setIconColor(differcost),
      upDownValue: `${setSlice(setString(differcost))} `,
    },
    {
      title: ' ',
      value: `${setSlice(setString(imp[0]))} `,
      upDownIcon: setIconColor(differimp),
      upDownValue: `${setSlice(setString(differimp))} `,
    },
    {
      title: ' ',
      value: `${click[0]}`,
      upDownIcon: setIconColor(differclick),
      upDownValue: `${setSlice(setString(differclick))}`,
    },
    {
      title: ' ',
      value: `${setSlice(setString(conversion[0]))}`,
      upDownIcon: setIconColor(differconversion),
      upDownValue: `${setSlice(setString(differconversion))}`,
    },
    {
      title: '',
      value: `${setSlice(setString(sales[0]))} `,
      upDownIcon: setIconColor(differsales),
      upDownValue: `${setSlice(setString(differsales))} `,
    },
  ]

  return (
    <>
      {adList.map((a, b) => {
        const key = `totalAd-${b}}`
        return (
          <div key={key} className={styles.wrap}>
            <h4>{a.title}</h4>
            <div className={styles.dataResult}>
              <p>{a.value}</p>
              <div className={styles.increase}>
                {a.upDownIcon}
                <p>{a.upDownValue}</p>
              </div>
            </div>
          </div>
        )
      })}
    </>
  )
}

export default AdData
