import { IForMatAdData } from '../../../../types/adDate.d'
import styles from './adCardItem.module.scss'

interface Props {
  item: IForMatAdData
}

const AdCardItem = ({ item }: Props): JSX.Element => {
  const onModify = () => {}
  return (
    <div className={styles.adCardItem}>
      <h2>
        <span>{item.adType === 'web' ? '_' : '_'}</span>
        {item.title}
      </h2>
      <div className={styles.contentsWrapper}>
        <dl>
          <div className={styles.dtddWrapper}>
            <dt></dt>
            {item.status === 'ended' ? <dd></dd> : <dd></dd>}
          </div>
          <div className={styles.dtddWrapper}>
            <dt> </dt>
            {item.endDate ? (
              <dd>
                {item.startDate}({item.endDate})
              </dd>
            ) : (
              <dd>{item.startDate}</dd>
            )}
          </div>
          <div className={styles.dtddWrapper}>
            <dt>  </dt>
            <dd>{item.budget}</dd>
          </div>
          <div className={styles.dtddWrapper}>
            <dt> </dt>
            <dd>{item.report.roas}%</dd>
          </div>
          <div className={styles.dtddWrapper}>
            <dt></dt>
            <dd>{item.report.convValue}</dd>
          </div>
          <div className={styles.dtddWrapper}>
            <dt></dt>
            <dd>{item.report.cost}</dd>
          </div>
        </dl>
        <button className={styles.modifyBtn} type='button' onClick={onModify}>
          
        </button>
      </div>
    </div>
  )
}
export default AdCardItem
